.button {
    padding: 5px 24px;
    gap: 10px;
    color: #DDEEFF;
    border-radius: 3px;
    background:  #036ad2;
}

.disabled {
    color: #001bcc;
background: darkgray;
    opacity: 0.5;

}

.secondary {
    background: #DDEEFF;
}

.default {
    background: #001bcc;
}

.red {
    color: #00cc22;
    background: #ff0404;
}

.default:hover {
    background: #0066CC;
}

.red:hover {
    background: #f37272;
}

.secondary:hover {
    background: #333338;
}


.default:active {
    background: #00cc22;
}

.secondary:active {
    background: #00cc22;
}

.red:active {
    background: rgba(124, 3, 3, 0.96);
}

.button:disabled {
    background: #e8e9ef;
}
